import React, { useEffect, useReducer } from 'react';
import NewsCard from './NewsCard'
import data from '../data'
import './News.css'

function GetDateTime(SK) {
    return (
        SK.split('_')[0]
    )
}

const initialState = {
    loading: true,
    error: '',
    articles: []
}

const articlesReducer = (state, action) => {
    switch (action.type) {
        case 'FETCH_SUCCESS':
            return {
                loading: false,
                error: '',
                articles: action.payload
            }
        case 'FETCH_ERROR':
            return {
                loading: false,
                error: 'something went wrong',
                articles: []
            }
        default:
            return state
    }
}

function News() {

    const [state, dispatch] = useReducer(articlesReducer, initialState);

    useEffect(() => {
        const currentDate = new Date().toJSON().slice(0, 10);
        fetch("https://ft5f6uj1n2.execute-api.eu-west-3.amazonaws.com/Prod/" + currentDate)
            .then(res => res.json())
            .then(res => {
                dispatch({ type: 'FETCH_SUCCESS', payload: res })
            })
            .catch(error => {
                dispatch({ type: 'FETCH_ERROR' })
            })
    }, []);

    return (
        <div className="news-flex">
            {state.loading ? (<h2>Loading ...</h2>) : state.articles.map((article) =>
                (
                    <NewsCard
                        key={article.uuid}
                        title={article.title}
                        description={article.contentSnippet}
                        link={article.link}
                        sourceName={article.source_name}
                        dateTime={GetDateTime(article.SK)}
                    />
                )
            )}
        </div>
    )
}

export default News


