import React from 'react';
import moment from 'moment'
import {faClock} from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


function toSnippet(content, maxLength) {
    return content.length >= maxLength ? content.substring(0, maxLength - 3) + "..." : content;
}

function NewsCard(props) {


    const randomImgLink = "https://picsum.photos/300/220?random=" + Math.floor(Math.random() * 100);
    const articleDate = new Date(props.dateTime).toString();
    const readableDate = moment(articleDate).format('LLLL');
    const relativeDate = moment(articleDate).fromNow();
    return (
        <div onClick={() => window.open(props.link, '_blank')} className="clickable news-card-flex shadowed">
            
            <div className = "article-title alternative-title">
                    <a href={props.link} target="_blank" rel="noopener noreferrer">{props.title}</a>
                </div>
            
            <div className="news-row">
            
            <div className="news-content">

                
                    
                
                <div title={readableDate} className ="article-header"> 
                    <FontAwesomeIcon icon={faClock} /> {relativeDate}&nbsp; - &nbsp;
                    <span className="source-header" href="#">@{props.sourceName}</span> 
                </div>

                <div className = "article-title main-title">
                    <a href={props.link} target="_blank" rel="noopener noreferrer">{props.title}</a>
                </div>
                
                    {toSnippet(props.description, 150)}
            </div>
            <div className="news-img-container">
                <img className="news-img" src={randomImgLink} alt="placeHolder"></img>
            </div>

            
            </div>
        </div>
    )
}

export default NewsCard